import { startOfDay, startOfWeek, startOfMonth, isWithinInterval, endOfDay, endOfWeek, endOfMonth } from 'date-fns';

/**
 * Ø¨Ø±Ø±Ø³ÛŒ Ù…ÛŒâ€ŒÚ©Ù†Ø¯ Ú©Ù‡ Ø¢ÛŒØ§ ÛŒÚ© ØªØ§Ø±ÛŒØ® Ù…Ø¹ÛŒÙ†ØŒ Ø§Ù…Ø±ÙˆØ² Ø§Ø³Øª ÛŒØ§ Ù†Ù‡.
 * @param date ØªØ§Ø±ÛŒØ® ÙˆØ±ÙˆØ¯ÛŒ (Ù…ÛŒâ€ŒØªÙˆØ§Ù†Ø¯ Ø±Ø´ØªÙ‡ ÛŒØ§ Date Ø¨Ø§Ø´Ø¯)
 */
export const isToday = (date: string | Date): boolean => {
  const dateObj = new Date(date);
  const todayStart = startOfDay(new Date());
  const todayEnd = endOfDay(new Date());
  return isWithinInterval(dateObj, { start: todayStart, end: todayEnd });
};

/**
 * Ø¨Ø±Ø±Ø³ÛŒ Ù…ÛŒâ€ŒÚ©Ù†Ø¯ Ú©Ù‡ Ø¢ÛŒØ§ ÛŒÚ© ØªØ§Ø±ÛŒØ® Ù…Ø¹ÛŒÙ†ØŒ Ø¯Ø± Ù‡ÙØªÙ‡ Ø¬Ø§Ø±ÛŒ Ø§Ø³Øª ÛŒØ§ Ù†Ù‡.
 * (Ù†Ú©ØªÙ‡: date-fns Ù‡ÙØªÙ‡ Ø±Ø§ Ø§Ø² ÛŒÚ©Ø´Ù†Ø¨Ù‡ Ø´Ø±ÙˆØ¹ Ù…ÛŒâ€ŒÚ©Ù†Ø¯. Ø¨Ø±Ø§ÛŒ Ø´Ù†Ø¨Ù‡ØŒ Ù†ÛŒØ§Ø² Ø¨Ù‡ ØªÙ†Ø¸ÛŒÙ…Ø§Øª locale Ø§Ø³Øª)
 */
export const isThisWeek = (date: string | Date): boolean => {
  const dateObj = new Date(date);
  const weekStart = startOfWeek(new Date(), { weekStartsOn: 6 }); // Ø´Ù†Ø¨Ù‡ Ø¨Ù‡ Ø¹Ù†ÙˆØ§Ù† Ø´Ø±ÙˆØ¹ Ù‡ÙØªÙ‡
  const weekEnd = endOfWeek(new Date(), { weekStartsOn: 6 });
  return isWithinInterval(dateObj, { start: weekStart, end: weekEnd });
};

/**
 * Ø¨Ø±Ø±Ø³ÛŒ Ù…ÛŒâ€ŒÚ©Ù†Ø¯ Ú©Ù‡ Ø¢ÛŒØ§ ÛŒÚ© ØªØ§Ø±ÛŒØ® Ù…Ø¹ÛŒÙ†ØŒ Ø¯Ø± Ù…Ø§Ù‡ Ø¬Ø§Ø±ÛŒ Ø§Ø³Øª ÛŒØ§ Ù†Ù‡.
 */
export const isThisMonth = (date: string | Date): boolean => {
  const dateObj = new Date(date);
  const monthStart = startOfMonth(new Date());
  const monthEnd = endOfMonth(new Date());
  return isWithinInterval(dateObj, { start: monthStart, end: monthEnd });
};